import React, {useState, useEffect} from 'react'
import './MiniCart.scss'
import {HiOutlineX} from 'react-icons/hi';

import LineCart from '../line-cart/LineCart'

function MiniCart({
    id,
    image,
    name,
    price,
    miniCartOpen,
    setMiniCartOpen,
    deviceWidth
}) {

    const [position,
        setPosition] = useState('-100%')
    const [opacity,
        setOpacity] = useState('0')

    function closePanel() {
        setOpacity('0')
        setPosition('-100%')
        setTimeout(() => {
            setMiniCartOpen(false)
        }, 300);
    }

    useEffect(() => {
        if (miniCartOpen) {
            setOpacity('0.5')
            setPosition('0')
        }

    }, [miniCartOpen])

    return (miniCartOpen
        ? <div onClick={() => closePanel()}
                style={{
                background: `rgba(0,0,0,${opacity})`
            }}
                className="mini-cart">
                <div onMouseLeave={() => closePanel()}
                    className="mini-cart-container"
                    style={deviceWidth < 768
                    ? {
                        bottom: position
                    }
                    : {
                        right: position
                    }}>
                    <div className="title">
                        <h2>Added to cart</h2>

                        <HiOutlineX onClick={() => closePanel()} className="icon"/>
                    </div>

                    <LineCart id={id} name={name} image={image} price={`${price.toFixed(2)} €`}/>
                </div>
            </div>
        : null)
}

export default MiniCart