import React, {useState, useEffect} from 'react'
import './ShoppingList.scss'
import Sort from '../sort/Sort'
import PlantItem from '../plant-item/PlantItem'
import plantList from '../../data/plantList'
import PlantDetail from '../plant-detail/PlantDetail'

function ShoppingList({cart, setCart, isPlp, setIsPlp, deviceWidth}) {

    const [isOpen] = useState(true)

    const [toShow,
        setToShow] = useState([])

    const [shoppingList,
        setShoppingList] = useState(plantList)

    const [miniCartOpen,
        setMiniCartOpen] = useState(false)

    const [overflow,
        setOverflow] = useState('scroll')
  

    useEffect(() => {
        if (miniCartOpen) {
            setOverflow("hidden")
        } else {
            setOverflow("scroll")
        }
    }, [miniCartOpen])

    document.body.style.overflowY = overflow;

    function addToCart(id, name, image, price) {
        const plantAdded = cart.find((plant) => plant.name === name)

        if (plantAdded) {
            const filteredPlantAdded = cart.filter((plant) => plant.name !== name);
            setCart([
                {
                    id,
                    name,
                    image,
                    price,
                    amount: plantAdded.amount + 1
                },
                ...filteredPlantAdded
            ]);
            setMiniCartOpen(true);
        } else {
            setCart([
                {
                    id,
                    name,
                    image,
                    price,
                    amount: 1
                },
                ...cart
            ]);
            setMiniCartOpen(true)
        }
    }

    function openDetail(id, image, name, price, offer, light, water) {

        window.scrollTo(0, 0)
        const plantToShow = shoppingList.filter((plant) => plant.id === id)

        if (plantToShow) {
            setToShow([
                {
                    id,
                    image,
                    name,
                    price,
                    offer,
                    light,
                    water
                }
            ])
            setIsPlp(false)
        }
    }

    return (
        <div className="ShoppingContainer">
            <Sort
                hide={isPlp
                ? 'flex'
                : 'none'}
                shoppingList={shoppingList}
                setShoppingList={setShoppingList}/> {isPlp
                ? <ul className="ShoppingList">
                        {shoppingList.map(({
                            id,
                            name,
                            image,
                            isSpecialOffer,
                            light,
                            water,
                            price
                        }) => <PlantItem
                            key={id}
                            image={image}
                            name={name}
                            price={price}
                            offer={isSpecialOffer}
                            light={light}
                            water={water}
                            isOpen={isOpen}
                            openPanel={() => openDetail(id, image, name, price, isSpecialOffer, light, water)}/>)}
                    </ul>
                : toShow.map(({
                    id,
                    image,
                    name,
                    price,
                    isSpecialOffer,
                    light,
                    water
                }) => <PlantDetail
                    key={id}
                    image={image}
                    name={name}
                    price={price}
                    isSpecialOffer={isSpecialOffer}
                    light={light}
                    water={water}
                    isOpen={isOpen}
                    onClick={() => addToCart(id, name, image, price)}
                    miniCartOpen={miniCartOpen}
                    setMiniCartOpen={setMiniCartOpen}
                    setPosition={() => setMiniCartOpen(false)}
                    closePanel={() => setIsPlp(true)}
                    deviceWidth={deviceWidth}/>)}

        </div>
    )
}

export default ShoppingList
