import React from 'react'
import margarita from '../../medias/margarita.svg';
import './Header.scss'
import {HiOutlineShoppingCart} from 'react-icons/hi';

function Header({cart, cartAmount, setIsOpen, setIsPlp}) {

    const title = "margarita"

    return (
        <div className="Header">
            <div className="logo" onClick={() => setIsPlp(true)}>
                <img className="App-logo" src={margarita} alt="margarita-logo"/>
                <h1>{title
                        .slice(0, 1)
                        .toUpperCase() + title
                        .slice(1)
                        .toLocaleLowerCase()}</h1>
            </div>
            <div
                className="cart-icon"
                onClick={() => setIsOpen(true)}
                onMouseOver={() => setIsOpen(true)}>
                {cart.length > 0 && <div className="cart-number">{cartAmount}</div>}
                <HiOutlineShoppingCart className="icon"/>
            </div>
        </div>
    )
}

export default Header