import React, {useState, useEffect} from 'react'
import './Cart.scss'
import LineCart from '../line-cart/LineCart'
import {HiOutlineX} from 'react-icons/hi';

function LineTotal({total}) {
    return (
        <div className="line-total">
            <p>
                Total
            </p>
            <p className="price">{total}</p>
        </div>
    )
}

function Cart({
    cart,
    setCart,
    deviceWidth,
    cartAmount,
    total,
    isOpen,
    setIsOpen
}) {

    const [position,
        setPosition] = useState('-100%')

    const [opacity,
        setOpacity] = useState('0')

    const [overflow,
        setOverflow] = useState('scroll')

    useEffect(() => {
        if (isOpen) {
            setOverflow("hidden")
        } else {
            setOverflow("scroll")
        }
    }, [isOpen])

    document.body.style.overflowY = overflow;

    useEffect(() => {
        isOpen && setTimeout(() => {
            setOpacity('0.5')
            setPosition('0')
        }, 300);

    }, [isOpen])

    function closePanel() {
        setOpacity('0')
        setPosition('-100%')
        setTimeout(() => {
            setIsOpen(false)
        }, 300);
    }

    useEffect(() => {
        if (total > 0) {
            document.title = `Margarita - ${total.toFixed(2)} €`
        }
    }, [total])

    function deleteToCart(id) {
        let filtered = cart.filter((plant) => plant.id !== id)
        setCart(filtered)
    }

    function removeItem(id) {
        const less = cart.filter(plant => plant.id === id)

        if (less[0].amount > 1) {
            less[0].amount -= 1
            setCart([...cart])
        }

    }
    function addItem(id) {
        const less = cart.filter(plant => plant.id === id)
        less[0].amount += 1
        setCart([...cart])
    }

    return isOpen
        ? (
            <div
                className="cart"
                style={{
                background: `rgba(0,0,0,${opacity})`
            }}>
                <div
                    className="cart-container"
                    onMouseLeave={() => closePanel()}
                    style={deviceWidth < 768
                    ? {
                        bottom: position
                    }
                    : {
                        right: position
                    }}>
                    <div className="title">
                        <h2>Cart ({cartAmount})</h2>
                        <HiOutlineX onClick={() => closePanel()} className="icon"/>
                    </div>
                    <div className="cart-content">
                        {cart.map(({
                            id,
                            name,
                            image,
                            amount,
                            price
                        }, index) => <LineCart
                            key={index}
                            id={id}
                            name={name}
                            image={image}
                            amount={amount}
                            remove={() => removeItem(id)}
                            add={() => addItem(id)}
                            erase={() => deleteToCart(id)}
                            price={`${ (amount * price).toFixed(2)} €`}/>)}
                        <LineTotal total={`${total.toFixed(2)} €`}/>
                    </div>

                </div>

            </div>
        )
        : null
}

export default Cart
