import React from 'react'
import './CareScale.scss'

function CareScale({scaleValue, careType}) {

    let info

    switch (scaleValue) {
        case 1:
            info = "sparsely";
            break;
        case 2:
            info = "moderate";
            break;
        case 3:
            info = "a lot of";
            break;
        default:
            break;
    }

    return (

        <div className="care">
            <p>{`This plant needs ${info} ${careType}`}</p>
        </div>
    )
}

export default CareScale