import React from 'react';
import './LineCart.scss';
import {HiOutlineTrash} from 'react-icons/hi';
import {HiOutlinePlusSm} from 'react-icons/hi';
import {HiOutlineMinusSm} from 'react-icons/hi';

function LineCart({
    id,
    name,
    image,
    amount,
    price,
    add,
    remove,
    erase
}) {
    return (
        <div id={id} className="line-cart">
            <img className="mini-image" src={image} alt={image}/>
            <div className="line-cart-infos">
                <h3>{name}</h3>
                {amount && <div className="counter">
                    <div className="mini-icon-container">
                    <HiOutlineMinusSm className="mini-icon" onClick={remove}/>
                    </div>
                    <p >{`${amount} `}</p>
                    <div className="mini-icon-container">
                    <HiOutlinePlusSm className="mini-icon" onClick={add}/>
                    </div>
                </div>}
                <p className="price">{price}</p>
            </div>
            {amount && <HiOutlineTrash className="icon erase" onClick={erase}/>
}
        </div>
    )
}

export default LineCart